import {Component, OnInit, Input} from '@angular/core';
import {IProductoRopa} from '../producto-ropa/interfaces';
import {CarritoService} from 'src/app/services/carrito.service';
import {registerLocaleData} from '@angular/common';
import es from '@angular/common/locales/es';

@Component({
    selector: 'app-producto-carrito',
    templateUrl: './producto-carrito.component.html',
    styleUrls: ['./producto-carrito.component.scss']
})
export class ProductoCarritoComponent implements OnInit {

    constructor(private carritoService:CarritoService) {
        registerLocaleData(es);
    }

    @Input() producto: IProductoRopa = {
        nombre: "producto",
        colores: [],
        fotoUrl: "",
        precio: 0,
        sizes: [],
        descripcion: '',
        id: "",
        cantidad: 1
    }

    ngOnInit(): void {
    }

    eliminarDeCarrito(p:IProductoRopa) {
        this.carritoService.eliminar(p);
    }

}
