import {Component, OnInit, Input} from '@angular/core';
import {IProductoRopa, IProductColor, IColor} from './interfaces';
import {CarritoService} from 'src/app/services/carrito.service';


@Component({
    selector: 'app-producto-ropa',
    templateUrl: './producto-ropa.component.html',
    styleUrls: ['./producto-ropa.component.scss']
})
export class ProductoRopaComponent implements OnInit {

    constructor(private carritoService: CarritoService) {}

    @Input() producto: IProductoRopa = {
        nombre: "producto",
        colores: [],
        fotoUrl: "",
        precio: 0,
        sizes: [],
        descripcion: '',
        id: "",
        cantidad: 1
    }

    selectedSize: string = "";

    public get colorList(): IProductColor[] {
        let ret: IProductColor[] = [];

        ret = this.producto.colores.map(p => {
            let pc: IProductColor = {
                color: {
                    codigo: p.codigo,
                    nombre: p.nombre
                },
                checked: false
            };
            pc.checked = this.producto.selectedColor? this.producto.selectedColor.codigo === p.codigo : false;
            return pc;
        })

        return ret;
    }

    public get sizes(): string[] {
        let ret: string[] = [];
        ret = this.producto.sizes;
        return ret;
    }

    agregarACarrito(p: IProductoRopa) {
        this.carritoService.add(p);
    }

    setColor(pColor: IProductColor) {
        this.producto.selectedColor = JSON.parse(JSON.stringify(pColor.color));
    }

    setSize(p: string) {
        this.selectedSize = p;
    }

    ngOnInit(): void {
    }

}
